@import "generic-container";

.np-search {
  color: $color-emphasis;
  background-color: rgba(0,0,0,0.75);
  width: 0;
  height: 0;
  top: 0;
  left: 0;
  > .content {
    height: deep-get($theme, 'lengths.grid') * 6;
    padding-top: deep-get($theme, 'lengths.grid');
    padding-bottom: deep-get($theme, 'lengths.grid');
  }
  .-no-script & {
    margin-bottom: -(deep-get($theme, 'lengths.grid') * 6);
  }
}

@media (max-width: deep-get($config, 'breakpoints.md')) {
  .np-search {
    > .content {
      width: deep-get($theme, 'lengths.grid') * 6;
      padding-top: deep-get($theme, 'lengths.grid');
      padding-left: 0;
    }

    &.-open {
      width: 100%;
      height: 100%;
    }

    &.-open, &.-always-open {
      > .content {
        width: 100%;
        padding-left: deep-get($theme, 'lengths.grid') * 6;
        background-color: deep-get($theme, 'colors.negative');
      }
    }

    .-no-script & > .content {
      width: auto;
    }
  }
}

