@import "generic-container";

.article-info {
  > .primary {
    @extend .generic-container;
  }
  > .title-container {
    height: deep-get($theme, 'lengths.grid') * 6;
    > .titles {
      height: deep-get($theme, 'lengths.grid') * 6;
      > .generic-container {
        > .title {
          height: (deep-get($theme, 'lengths.grid') * 3) + 3px;
        }
        > .subtitle {
          height: deep-get($theme, 'lengths.grid') * 3;
        }
      }
    }
  }
}
