 * @date 2017-01-28
 */

::selection {
  background-color: #fff;
  color: #333;
}

.np-view {
  > .toggle {
    height: deep-get($theme, 'lengths.grid') * 6;
    padding-top: deep-get($theme, 'lengths.grid');
    padding-bottom: deep-get($theme, 'lengths.grid');
  }

  > .actions {
    background-color: darken(deep-get($theme, 'colors.negative'), 5%);
    height: deep-get($theme, 'lengths.grid') * 10;
    & + .main {
      margin-bottom: deep-get($theme, 'lengths.grid') * 16;
    }
  }

  > .main {
    margin-top: deep-get($theme, 'lengths.grid') * 6;
    margin-bottom: deep-get($theme, 'lengths.grid') * 6;
  }
}

@media (max-width: deep-get($config, 'breakpoints.md')) {
  .np-view {
    &.-sidebar-open .single-article.-current .article-info > .title-container > .titles {
      padding-left: deep-get($theme, 'lengths.grid') * 6;
      padding-right: deep-get($theme, 'lengths.grid') * 6;
    }

    &.-search-open > .search {
      width: 100%;
      padding-left: deep-get($theme, 'lengths.grid') * 6;
    }
  }
}
