.form-control {
  padding: (deep-get($theme, 'lengths.grid') / 2) deep-get($theme, 'lengths.grid');
  background-color: darken(deep-get($theme, 'colors.negative'), 5%);
}

textarea.form-control {
  line-height: deep-get($theme, 'type.fonts.body.line-height') * (8 / 9);
}

.np-actions .form-control {
  background-color: darken(deep-get($theme, 'colors.negative'), 10%);
}
