.generic-container {
  padding-left: deep-get($theme, 'lengths.grid') * 4;
  padding-right: deep-get($theme, 'lengths.grid') * 4;
}

@media (max-width: deep-get($config, 'breakpoints.lg')) {
  .generic-container {
    padding-left: deep-get($theme, 'lengths.grid') * 2;
    padding-right: deep-get($theme, 'lengths.grid') * 2;
  }
}

@media (max-width: deep-get($config, 'breakpoints.md')) {
  .generic-container {
    padding-left: deep-get($theme, 'lengths.grid');
    padding-right: deep-get($theme, 'lengths.grid');
  }
}
