@import "generic-container";

.menu-item {
  > .link > .caption {
    @extend .generic-container;
    padding-top: deep-get($theme, 'lengths.grid');
    padding-bottom: deep-get($theme, 'lengths.grid');
  }
  &.-image {
    @extend .generic-container;
    height: deep-get($theme, 'lengths.grid') * 12;
    //padding: 0 (deep-get($theme, 'lengths.grid') / 2);
    //margin-bottom: deep-get($theme, 'lengths.grid');
    margin-bottom: 4px;
    > .link > .caption {
      padding: (deep-get($theme, 'lengths.grid') / 2) (deep-get($theme, 'lengths.grid'));
      background-color: rgba(0,0,0,0.5);
    }
  }
}
