/**
 * Style description.
 * @author TheoryOfNekomata
 * @date 2017-02-07
 */

@import "../../../node_modules/rainbow-code/themes/sass/init";

.code-block {
  background-color: darken(deep-get($theme, 'colors.negative'), 2.5%);
  margin-bottom: 1em;
}

pre {
  background-color: transparent;
  word-wrap: break-word;
  padding: 0;
  color: #fff;
  margin: deep-get($theme, 'lengths.grid');
  line-height: deep-get($theme, 'type.fonts.code.line-height');
}

pre, code {
  font-family: deep-get($theme, 'type.fonts.code.font-family');
  //font-size: deep-get($theme, 'type.font-size') * 1.2 !important;
  font-size: deep-get($theme, 'type.font-size') !important;
}


.comment {
  color: #75715E;
}

.constant {
  color: #AE81FF;
}

.storage {
  color: #ff4389;
  font-style: normal;
  font-weight: bold;
}

.operator.dot {
  color: #76a7d2;
}

.entity, .support.attribute, .entity.attribute {
  color: #67c252;
  font-style: italic;
}

.string, .comment.docstring {
  color: #eed371;
}

.support.tag {
  color: #fff;
}

.keyword {
  color: #ff4389;
  font-style: normal;
  font-weight: bold;
}

.selector, .storage.modifier.extends {
  color: #ff4389;
}

.inherited-class {
  font-style: italic;
}

.operator, .support.tag-name, .entity.tag {
  color: #ff4389;
}

.entity.name.type, .entity.name.struct {
  color: #fff;
}

.support, *[data-language="c"] .function.call {
  color: #66D9EF;
}

.variable.global {
  color: #c28050;
}

.variable.global, .variable.class, .variable.instance {
  color: #A6E22E;
}

@include language("html") {
  .support.operator {
    color: #fff;
  }
}

@include language(("javascript", "js")) {
  .variable.super {
    color: #66D9EF;
  }
}
