/**
 * Style description.
 * @author TheoryOfNekomata
 * @date 2017-01-28
 */

@import "generic-container";

.single-article {
  margin-top: deep-get($theme, 'lengths.grid') * 4;
  margin-bottom: deep-get($theme, 'lengths.grid') * 4;
  &.-search-results > .results {
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 0;
  }

  &.-current .article-info > .title-container > .titles {
    padding-left: 33.333333333333%;
    padding-right: 33.333333333333%;
    background-color: deep-get($theme, 'colors.negative');
  }

  > .header > .actions {
    @extend .generic-container;
  }
  > .section {
    @extend .generic-container;
  }
}

@media (max-width: deep-get($config, 'breakpoints.xxl')) {
  .single-article {
    &.-current .article-info > .title-container > .titles {
      padding-left: 25%;
    }

    .-sidebar-open &.-current .article-info > .title-container > .titles {
      padding-left: 33.333333333333%;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.xl')) {
  .single-article {
    &.-current .article-info > .title-container > .titles {
      padding-left: 16.666666666667%;
    }

    .-sidebar-open &.-current .article-info > .title-container > .titles {
      padding-left: 33.333333333333%;
    }
  }
}

@media (max-width: deep-get($config, 'breakpoints.md')) {
  .single-article {
    &.-current .article-info > .title-container > .titles {
      padding-left: deep-get($theme, 'lengths.grid') * 6;
      padding-right: deep-get($theme, 'lengths.grid') * 6;
    }

    .-sidebar-open &.-current .article-info > .title-container > .titles {
      padding-left: deep-get($theme, 'lengths.grid') * 6;
      padding-right: deep-get($theme, 'lengths.grid') * 6;
    }
  }
}
