.modal-actions {
  padding-left: deep-get($theme, 'lengths.grid');
  padding-right: deep-get($theme, 'lengths.grid');
  margin-bottom: 0;
  margin-top: 0;
  text-align: right;
  > .action {
    display: inline-block;
    > .button {
      border: none;
      background-color: transparent;
      color: inherit;
      font-family: inherit;
      text-transform: uppercase;
      padding: (deep-get($theme, 'lengths.grid'));
    }
  }
}
