/**
 * Style description.
 * @author TheoryOfNekomata
 * @date 2017-02-03
 */

$seek-height: deep-get($theme, 'lengths.grid') / 3 * 2;
$scale-factor: 2;

.noUi-connect {
  border-radius: ($seek-height / 2) 0 0 ($seek-height / 2);
  margin-right: -($seek-height / 2);
  background-color: deep-get($theme, 'colors.positive');
}

.noUi-handle {
  background-color: $color-emphasis;
}

.range-input {
  &.-accent .noUi-connect {
    background-color: deep-get($theme, 'colors.primary');
  }
}

.noUi-target {
  background-color: lighten(deep-get($theme, 'colors.negative'), 5%);
  border-radius: ($seek-height / 2);
  height: $seek-height;
}

.noUi-horizontal {
  padding-right: $seek-height;

  .noUi-handle {
    width: $seek-height;
    height: $seek-height;
  }

  .noUi-origin {
    right: -($seek-height);
  }

  &:hover, &:focus, &:active {
    padding-right: $seek-height / 2 * $scale-factor;
    .noUi-origin {
      right: -($seek-height * $scale-factor);
    }
    .noUi-handle {
      width: $seek-height * $scale-factor;
      height: $seek-height * $scale-factor;
      top: 0 - ($seek-height / 4 * $scale-factor);
      bottom: 0 - ($seek-height / 4 * $scale-factor);
      left: -($seek-height / 4 * $scale-factor);
    }
  }
}
