.nav-sidebar {
  .breadcrumb-nav {
    text-transform: lowercase;
    > .bread {
      font-size: 75%;
    }
  }

  > .content > .group:last-child {
    margin-bottom: deep-get($theme, 'lengths.grid') * 6;
  }

  &.-with-header {
    padding-top: deep-get($theme, 'lengths.grid') * 6;
    > .header {
      height: deep-get($theme, 'lengths.grid') * 6;
    }
  }
}
