/**
 * Style description.
 * @author TheoryOfNekomata
 * @date 2017-01-27
 */

.theme-base {
  background-color: deep-get($theme, 'colors.negative');
  color: deep-get($theme, 'colors.positive');
  font-family: deep-get($theme, 'type.fonts.body.font-family');
  font-weight: deep-get($theme, 'type.fonts.body.font-weight');
  line-height: deep-get($theme, 'type.fonts.body.line-height');

  a:link,
  a:visited {
    color: deep-get($theme, 'colors.primary');
    text-decoration: none;
  }

  ._heading {
    color: $color-emphasis;
    font-family: deep-get($theme, 'type.fonts.headings.font-family');
    font-weight: deep-get($theme, 'type.fonts.headings.font-weight');
    line-height: deep-get($theme, 'type.fonts.headings.line-height');
    text-transform: deep-get($theme, 'type.fonts.headings.text-transform');
  }

  h1 {
    @extend ._heading;
  }

  h2 {
    @extend ._heading;
  }

  h3 {
    @extend ._heading;
  }

  h4 {
    @extend ._heading;
  }

  h5 {
    @extend ._heading;
  }

  h6 {
    @extend ._heading;
  }
}
