 * @date 2017-01-27
 */


$grid: deep-get($theme, 'lengths.grid');

.icon-text {
  width: $grid * 4;
  height: $grid * 4;
  > .text {
    font-size: $grid;
    padding-top: $grid / 8;
    padding-bottom: $grid / 8;
    font-family: deep-get($theme, 'type.fonts.body.font-family');
    &:first-child {
      top: 0;
    }
  }
  > .icon-wrapper {
    font-size: $grid * 2;
    font-family: deep-get($theme, 'type.fonts.headings.font-family');
    > .icon {
      &.lnr {
        font-size: $grid * 1.5;
        &.lnr-menu {
          font-size: $grid * 2;
        }
      }
    }
    &:first-child {
      font-size: $grid * 3;
      > .icon {
        &.lnr {
          font-size: $grid * 2;
        }
      }
    }
  }
}
