.theme-button {
  border: 1px solid;
  background-color: darken(deep-get($theme, 'colors.negative'), 3.5%);
  padding: (deep-get($theme, 'lengths.grid') / 2) (deep-get($theme, 'lengths.grid'));
  &.-link {
    border-color: transparent;
    color: deep-get($theme, 'colors.primary');
    background-color: transparent;
  }
}
