@import "generic-container";

.modal-content {
  > .section {
    width: deep-get($theme, 'lengths.grid') * 50;
    background-color: darken(deep-get($theme, 'colors.negative'), 10%);
    &:first-child {
      margin-top: deep-get($theme, 'lengths.grid') * 6;
    }
    &:last-child {
      margin-bottom: deep-get($theme, 'lengths.grid') * 6;
    }
  }
  > .header > .title {
    padding-left: deep-get($theme, 'lengths.grid');
    padding-right: deep-get($theme, 'lengths.grid');
    padding-top: deep-get($theme, 'lengths.grid');
    padding-bottom: deep-get($theme, 'lengths.grid');
  }
}
